import { Box, Chip } from '@material-ui/core';
import {
  Link,
  ResponseErrorPanel,
  Table,

} from '@backstage/core-components';
import {

  PullRequestStatus,
} from '@backstage/plugin-azure-devops-common';
import React, { useState } from 'react';

import { AzurePullRequestsIcon } from '../AzurePullRequestsIcon';
import { DateTime } from 'luxon';
import { PullRequestStatusButtonGroup } from '../PullRequestStatusButtonGroup';
import { useEntity } from '@backstage/plugin-catalog-react';
import { usePullRequests } from '../../hooks/usePullRequests';

const columns = [
  {
    title: 'ID',
    field: 'pullRequestId',
    highlight: false,
    width: 'auto',
  },
  {
    title: 'Title',
    field: 'title',
    width: 'auto',
    render: (row) => (
      React.createElement(Box, { display: "flex", alignItems: "center",}
        , React.createElement(Link, { to: row.link ?? '',}, row.title)
        , row.isDraft && (
          React.createElement(Box, { paddingLeft: 1,}
            , React.createElement(Chip, {
              label: "Draft",
              variant: "outlined",
              color: "secondary",
              size: "small",}
            )
          )
        )
      )
    ),
  },
  {
    title: 'Source',
    field: 'sourceRefName',
    width: 'auto',
  },
  {
    title: 'Target',
    field: 'targetRefName',
    width: 'auto',
  },
  {
    title: 'Created By',
    field: 'createdBy',
    width: 'auto',
  },
  {
    title: 'Created',
    field: 'creationDate',
    width: 'auto',
    render: (row) =>
      (row.creationDate
        ? DateTime.fromISO(row.creationDate)
        : DateTime.now()
      ).toRelative(),
  },
];





export const PullRequestTable = ({ defaultLimit }) => {
  const [pullRequestStatusState, setPullRequestStatusState] =
    useState(PullRequestStatus.Active);
  const { entity } = useEntity();

  const { items, loading, error } = usePullRequests(
    entity,
    defaultLimit,
    pullRequestStatusState,
  );

  if (error) {
    return React.createElement(ResponseErrorPanel, { error: error,} );
  }

  return (
    React.createElement(Table, {
      isLoading: loading,
      columns: columns,
      options: {
        search: true,
        paging: true,
        pageSize: 5,
        showEmptyDataSourceMessage: !loading,
      },
      title: 
        React.createElement(Box, { display: "flex", alignItems: "center",}
          , React.createElement(AzurePullRequestsIcon, { style: { fontSize: 30 },} )
          , React.createElement(Box, { mr: 1,} ), "Azure Repos - Pull Requests ("
               , items ? items.length : 0, ")"
          , React.createElement(Box, { position: "absolute", right: 320, top: 20,}
            , React.createElement(PullRequestStatusButtonGroup, {
              status: pullRequestStatusState,
              setStatus: setPullRequestStatusState,}
            )
          )
        )
      ,
      data: items ?? [],}
    )
  );
};
